/**
 * 
 * @param {Object} value1
 * @param {Object} value2
 */
function moneySum(value1,value2){
	return (parseFloat(value1)+parseFloat(value2)).toFixed(2);
}
/**
 * 
 * @param {Object} value1
 * @param {Object} value2
 */
function moneySub(value1,value2){
	let res = (parseFloat(value1)-parseFloat(value2)).toFixed(2);
	return res>0?res:0;
}

