/**
 * 
 */
function checkSession() {
	return new Promise((resolve, reject) => {
		wx.checkSession({
			success(res) {
				//session_key 
				let code = uni.getStorageSync('code') || '';
				// TODO:code==
				resolve(code);
			},
			fail: async (err) => {
				// session_key 
				let code = await onLogin();
				reject(code);
			}
		})
	})
}

/**
 * code
 */
function onLogin() {
	return new Promise((resolve, reject) => {
		uni.login({
			provider: 'weixin',
			success: function(res) {
				if (res.errMsg == "login:ok") {
					uni.setStorageSync('code', res.code);
					resolve(res.code);
				} else {
					reject(res);
				}
			},
			fail: function(err) {
				reject(res);
			}
		});
	})
}

module.exports = {
	checkSession,
	onLogin
};
