import * as common from './common.js' //common
import * as db from './db.js' //db

export const domain = 'https://wxhyx.aisspc.cn'
const http = domain + '/api/';
//POST
const post = (method, data, callback) => {
	uni.showLoading({
		title: ''
	});
	
	let userinfo = db.get("userinfo");
	
	if (userinfo) {
		data.token = userinfo.token;
	}
	
	uni.request({
		url: http + method,
		header: {
			'Content-Type': 'application/x-www-form-urlencoded', //
		},
		data: data,
		method: 'POST',
		success: (response) => {
			uni.hideLoading();
			const result = response.data			
			// 
			if (result.code === 401) {
				//db.del("userinfo");
				uni.showToast({
					title: result.msg,
					icon: 'none',
					duration: 1000,
					complete: function() {
						setTimeout(function() {
							uni.hideToast();






							uni.navigateTo({
								url: '/pages/login/base',
								animationType: 'pop-in',
								animationDuration: 200
							});

						}, 1000)
					}
				});
			}
			callback(result);
			
		},
		fail: (error) => {
			uni.hideLoading();
			if (error && error.response) {
				showError(error.response);
			}
		},
	});

}
//GET
const get = (url, data, callback) => {
	
	uni.showLoading({
		title: ''
	});	
	let header = {
		'Content-Type': 'application/x-www-form-urlencoded', //
	}
	// token
	let userinfo = db.get("userinfo");
	
	if (userinfo) {
		header.token = userinfo.token;
	}
	

	
	let param = common.builderUrlParams(http + url, data)
	
	// console.log(url, data, param, header);
	
	uni.request({
		url: param,
		header: header,
		method: 'GET',
		success: (response) => {
			uni.hideLoading();
			const result = response.data
			if (result.code === 401) {
				//db.del("userinfo");
				uni.showToast({
					title: result.msg,
					icon: 'none',
					duration: 1000,
					complete: function() {
						setTimeout(function() {
							uni.hideToast();






							uni.navigateTo({
								url: '/pages/login/base',
								animationType: 'pop-in',
								animationDuration: 200
							});

						}, 1000)
					}
				});
			}
			callback(result);
		},
		fail: (error) => {
			uni.hideLoading();
			if (error && error.response) {
				showError(error.response);
			}
		}
	});
}
//
const showError = error => {
	let errorMsg = ''
	switch (error.status) {
		case 400:
			errorMsg = ''
			break
		case 401:
			errorMsg = ''
			break
		case 403:
			errorMsg = ''
			break
		case 404:
			errorMsg = `: ${error.config.url}`
			break
		case 408:
			errorMsg = ''
			break
		case 500:
			errorMsg = ''
			break
		case 501:
			errorMsg = ''
			break
		case 502:
			errorMsg = ''
			break
		case 503:
			errorMsg = ''
			break
		case 504:
			errorMsg = ''
			break
		case 505:
			errorMsg = 'HTTP'
			break
		default:
			errorMsg = error.msg
			break
	}

	uni.showToast({
		title: errorMsg,
		icon: 'none',
		duration: 1000,
		complete: function() {
			setTimeout(function() {
				uni.hideToast();
			}, 1000);
		}
	});
}
// 
// 
export const uploadImage = (num, callback) => {
	let userinfo = db.get("userinfo");

	if (!userinfo) {
		common.jumpToLogin();
		return false;
	}
	uni.chooseImage({
		count: num,
		success: (res) => {
			uni.showLoading({
				title: '...'
			});
			let tempFilePaths = res.tempFilePaths
			for (var i = 0; i < tempFilePaths.length; i++) {
				uni.uploadFile({
					url: domain + '/addons/qiniu/index/upload',
					filePath: tempFilePaths[i],
					fileType: 'image',
					name: 'file',
					headers: {
						'Accept': 'application/json',
						'Content-Type': 'multipart/form-data',
					},
					formData: {
						'method': 'images.upload',
						'upfile': tempFilePaths[i],
						'token': userinfo.token
					},
					success: (uploadFileRes) => {
						callback(JSON.parse(uploadFileRes.data),tempFilePaths)
					},
					fail: (error) => {
						if (error && error.response) {
							showError(error.response);
						}
					},
					complete: () => {
						setTimeout(function() {
							uni.hideLoading();
						}, 250);
					},
				});
			}
		}
	});
}

